都围绕着先锋的“五会”而展开：学会做人、学会思考、学会学习、学会交流、学会合作。\n              让每位学子找到自己的人生目标，快乐、自信地成为独立而有思想的人材，是先锋教育的首要目的，而留学只是我们架设的一座桥梁。\n\n              这就是先锋的教育，迥异于我们以前所理解的学习方式。我们的课堂从教室到公园，到博物馆，到大学校园，有远近的城市、乡村的学校，还有遍布城市各个角落的公益组织。")]),_c('p',[_vm._v("\n              我们的老师，有中国人、外国人，有硕士、博士、还有同学、乡民、小学生、行业精英、NGO成员……还有最重要的老师——大自然。\n              我们对中西方文化兼容并蓄，并践行古老的中国教育智慧——有教无类、因材施教，在充分给予每位学子个性化指导的同时，导师以身作则，化人于无声、于无形。\n            ")]),_c('p',[_vm._v("\n              期待有思想、有远见的你加入先锋！Looking forward to meeting you!\n            ")]),_c('div',{staticClass:"grid-single"},[_c('div',{staticClass:"single-one"},[_c('span',[_c('i',{staticClass:"fa fa-calendar"}),_vm._v("2018/07/08 ")])]),_c('div',{staticClass:"single-one"},[_c('span',[_c('i',{staticClass:"fa fa-user"}),_vm._v("先锋教育")])]),_c('div',{staticClass:"clearfix"})])])}]

